.imgAndDescriptionContainer {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: row;
}

.btn{
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0b64cc;
    border-color: #0b64cc;
    color: #fff;
    font-weight: 700;
    height: 45px;
    width: 120px;
    border-radius: 5px;
    text-decoration: none;
}

.imgAndDescriptionContainer img{
    padding-right: 90px;
}

.imgAndDescriptionContainer h2{
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    color: #282c47;
    font-size: calc(40px + 14 * ( (100vw - 576px) / 1024));
}


.titleAndDescription{
    height: 350px;
    width:500px;
    padding-top: 50px;
    line-height: 1.65;
}