.NavBlockBox {
    width: 100%;
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 1;

    position:fixed;
    background-color: white;
    border: 1px solid black;
    margin-top: -1px;
}

.navBlockLink{
    padding-right: 20px;
    margin-top: 5px;
}

.navBlockLink a {
    color: #282c47;
    text-decoration: none;
    font-family: 'Roboto Condensed', sans-serif;
    font-style: inherit;
    line-height: 2em;
    padding: 5px 20px;
    font-size: 15px
}

.title {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #282c47;
    text-align: center;
}
.title h1 {
    font-size: 30px;
    margin-top: 0;
    font-family: 'Roboto Condensed', sans-serif;
    margin-bottom: -15px;
}


.title span {
    margin-top: -15px;
    font-size: 10px;

}

.title a {
    text-decoration: none;
    color: #282c47;
    margin-left: 30px;
}
